.symbol-panel {
  width: 331px;

  .sp-main {
    .future-zone-tab,.option-panel-tab {
      margin-top: 24px;

      .tab-header {
        margin: auto;
        .active,.inactive {
          border-radius: 4px;
        }
      }
    }    

    .symbols {
      margin: 24px 0;
      .s-body {
        max-height: 400px;
        overflow: auto;
        -ms-overflow-style: none;
        scrollbar-width: none;
  
        /* Firefox */
        &::-webkit-scrollbar {
          display: none;
          /* Safari and Chrome */
        }
      }
      .symbol-row:last-child {
        border-bottom: 1px solid #0A172A80;
      }
      .symbol-search {
        margin-bottom: 24px;
      }
      .symbol-search .search-token {
        border: 1px solid transparent;
      }
      .symbol-search .search-token:hover {
        border: 1px solid #CD7A37;
      }
      .symbol-row {
        height: 64px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border: 1px solid transparent;
        border-top: 1px solid #0A172A80;
        padding: 0 16px;
        // padding: 0 16px;

        .ss-name {
          font-size: 16px;
          display: flex;
          width: 55%;
          align-items: center;

          img {
            margin-right: 8px;
          }
        }

        .ss-value {
          width: 25%;
          font-size: 14px;
          display: flex;
          // align-items: center;
          flex-direction: column;
          .mark-price {
            display: flex;
            align-items: center;
            // width: 100%;
            justify-content: end;
            text-align: right;
          }
          .volume24-changed {
            display: flex;
            align-items: center;
            // width: 100%;
            justify-content: end;
            margin-left: 2px;
          }
          .volume24.down {
            color: #CE5E5E;
          }
          .volume24.up {        
            color : #59AE99;
          }
          .direction {
            display: flex;
          }
          .volume24 {
            text-align: center;
            margin-left: 2px;
          }
        }
      }

      .symbol-row.selected {
        border: 1px solid #CD7A37;
        border-radius: 4px;
      }

      .symbol-row:hover {
        border: 1px solid rgba(205, 122, 55, 0.5);
        cursor: pointer;
        border-radius: 4px;
      }
    }

    .option-symbols {
      color: #E0ECFF;
      font-size: 16px;

      .accordion__heading {
        height: 64px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.15);
        display: flex;
        align-items: center;
        padding-left: 16px;
        cursor: pointer;

        .accordion__button {
          width: 100%;
          height: 100%;
          display: flex;
          align-items: center;
        }

        .accordion__button:after {
          display: inline-block;
          content: '';
          height: 10px;
          width: 10px;
          margin-right: 12px;
          border-bottom: 2px solid currentColor;
          border-right: 2px solid currentColor;
          transform: rotate(-45deg);
          position: absolute;
          right: 0;
          margin-right: 24px;
        }

        .accordion__button[aria-expanded='true']::after,
        .accordion__button[aria-selected='true']::after {
          transform: rotate(45deg);
        }
      }

      .accordion__panel {
        .option-row {
          height: 56px;
          padding-left: 32px;
          display: flex;
          border: 1px solid #203B60;
          justify-content: space-between;
          align-items: center;
          margin: 0 8px;
          border-radius: 4px;
          user-select: none;  

          img {
            margin: 0 4px;
          }

          .option-name {
            width: 45%;
            display: flex;
            align-items: center;
          }

          .option-value {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 55%;
            .mark-price {
              width: 45%;
            }
            .volume24 {
              width: 40%;
              text-align: center;
            }
            .direction {
              width: 15%;
            }
          }
        }

        .option-row:hover {
          border: 1px solid rgba(205, 122, 55, 0.5);
          cursor: pointer;
        }
      }
    }
  }

  .open-list {
    width: 81px;
    height: 18px;
    border: 1px solid #93A1B8;
    border-radius: 100px;
    text-align: center;
    line-height: 18px;
    display: inline-block;
  }
}